import { Component, AfterContentInit } from '@angular/core';
import { BlogService } from '../../services/blog';

declare var $: any;

@Component({
    selector: 'home',
    templateUrl: 'src/components/home/index.html',
    providers: [ BlogService ]
})

export class Home implements AfterContentInit {
    blogs: any = [];
    constructor(private blogService: BlogService) {}

    ngAfterContentInit() {
        this.blogService.getBlogs()
            .then((blogs) => { this.blogs = blogs; })
            .then(function() {
                setTimeout(function() {
                    $('.vl-data-slick').slick({
                        infinite: true,
                        slidesToShow: 4,
                        slidesToScroll: 4,
                        responsive: [
                            {
                                breakpoint: 768,
                                settings: {
                                    slidesToShow: 3,
                                    slidesToScroll: 3
                                }
                            }, {
                                breakpoint: 600,
                                settings: {
                                    slidesToShow: 2,
                                    slidesToScroll: 2
                                }
                            }, {
                                breakpoint: 480,
                                settings: {
                                    slidesToShow: 1,
                                    slidesToScroll: 1
                                }
                            }
                        ]
                    });
                }, 500);
            })
            .catch((err) => { console.error(err); });

    }
}
